import {Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
@Component({
    //template : 'testaaaa <span>aate st</span> <a href="#">ttest</a>',
    selector: 'app-person',
    //templateUrl: '../person/person.component.html',
    template: '<h3>{{ value }}</h3><ng-content select=".abc"></ng-content><ng-content select=".ghk"></ng-content><ng-content select=".def"></ng-content>',
    styleUrls: ['../person/person.component.css']
})
export class PersonComponent implements OnInit {
    
    @Input() name : string;
    @Input() age : number;
    
    @Output() removePerson = new EventEmitter();
    @Output() myClick = new EventEmitter();
    ngOnInit(){ }
    removeByClick() { this.removePerson.emit(this.name); }
    
    addClick() {this.myClick.emit(true);}
    subClick() {this.myClick.emit(false);}
    
    value = 1;
    
}
