import {Component, OnInit, ViewChild} from '@angular/core';
import {PersonComponent} from '../person/person.component';
@Component({
    //template : 'testaaaa <span>aate st</span> <a href="#">ttest</a>',
    selector: 'app-list-person',
    templateUrl: '../list-person/list-person.component.html',
    styleUrls: ['../list-person/list-person.component.css']
})
export class ListPersonComponent implements OnInit {
    
    ngOnInit(){ 
        
        function wait(ms) {
            return new Promise(r => setTimeout(r, ms))  
            //return setTimeout('', ms);
        }
        async function main() {
            console.log('sắp rồi...')
            await wait(2007)
            console.log('chờ tí...')
            await wait(2012)
            console.log('thêm chút nữa thôi...')
            await wait(2016)
            console.log('xong rồi đấy!')
        }
        main();
    }
    @ViewChild(PersonComponent) myPerson : PersonComponent;
    addValueByViewChild() { this.myPerson.value++; }
    val_dem = 0;
    constructor(){};
    arrPerson = [
        { name:'Tí', age:'18'},
        { name:'Tèo', age:'19'},
        { name:'Tửng', age:'20'},
    ]
    addDem() {this.val_dem = this.val_dem+1;}
    removePersonByName(name: string)
    {
        const index = this.arrPerson.findIndex(e => e.name === name);
        this.arrPerson.splice(index,1);
    }
    addPerson(str_type: boolean) { 
        if(str_type==true) { this.val_dem = this.val_dem + 1;  }
        else { this.val_dem = this.val_dem - 1;  }
    }
}
